<template>
<div class='orderpage'>
<div class='left'>
  <Card class='left-card'>
               <p slot="title">购物车</p>
                <a slot="extra"  @click="clearCartButton">清空</a>
                <template v-for="foodVo in shopcartList">
                 <div :key="foodVo.foodId" >
                     <hr />
                   <Row >                      
                    <Col span="5">{{foodVo.foodName}}</Col>
                    <Col span="10">
                    <ButtonGroup :key="foodVo.foodId" >
        <Button type="primary" @click="cartReduceOneFood(foodVo)">-</Button>
         <Button >{{foodVo.foodNum}}</Button>                                                                                                              
        <Button type="primary"  @click="foodAddNum(foodVo)">+</Button>
    </ButtonGroup></Col>
                     <Col span="5">数量:{{foodVo.foodNum}}</Col>     
                   </Row>                    
                </div>   
                </template>
                 <hr>
                <b class='allPrice'>￥{{cartObj.allPrice}}</b>
            </Card>
</div>
<div class='right'>
<p class="pp">收货地址</p>
<RadioGroup v-model="selectAddressId">
    <template v-for="address in addressList">
        <Radio :label="address.addressId" :key="address.addressId">
        <Card class='card'>              
                <p>{{address.userName}}</p>
                <p>{{address.userPhone}}</p>
                <p>{{address.userAddress}}</p>
        </Card>  
        </Radio>      
        </template>
    </RadioGroup>
    <br>
     <Button type="primary" @click="addButton">添加新地址</Button>
     <Form  label-position="left" :label-width="100">
        <FormItem label="送达时间">
          <TimePicker confirm placeholder="Select time" style="width: 168px" format="HH:mm" :value='chooseTime'   @on-change="handleChange"></TimePicker>
        </FormItem>
        <FormItem label="订单备注">
            <Input v-model="orderRemark"></Input>
        </FormItem>
    </Form>
    <alipay-img @ok='ok' @cancel='cancel' >确认下单</alipay-img>
  </div>  
</div>   
</template>
<script>
import {selectAddressMany,selectOneCart,foodAddNum,foodReduceOneNum,clearCart,addOneOrder} from '@/api/api.js'
import {AlipayImg} from '_c/easyCo/index.js'
export default {
    components:{AlipayImg},
    data(){
        return{
           selectAddressId:'',
           user:'',
           addressList:[],
           shopcartList:[],
           cartObj:'',
           orderRemark:'',
           nowHour:'',
           nowMinute:'',
           nowDay:'',
           timeList:[],
           chooseTime:'',
        }
    },
    mounted(){
        let myDate=new Date()
        this.nowHour=myDate.getHours()
        this.nowMinute=myDate.getMinutes()
        this.nowDay=myDate.toLocaleDateString()   
        this.user=JSON.parse(localStorage.getItem('user'))
        let data={userId:this.user.userId}
        selectAddressMany(data).then(res=>{
            this.addressList=res.data.data
            if(this.addressList.length==0){
                this.$Message.error('请先添加地址')
            }
        })
         //查询这个用户的购物车
        let cardData={userId:this.user.userId}
        selectOneCart(cardData).then(res=>{           
            let result=res.data.data
            this.shopcartList=result.foodVoList
            this.cartObj=result.cart
        })
    },
    methods:{
           //往购物车里添加某样食品
        foodAddNum(foodVo){
            let data={foodId:foodVo.foodId,cartId:this.user.cartId,userId:this.user.userId}
            foodAddNum(data).then(res=>{               
                let result=res.data.data
                this.shopcartList=result.foodVoList
                this.cartObj=result.cart
            })
        },

        cartReduceOneFood(foodVo){
            let data={foodId:foodVo.foodId,cartId:this.user.cartId,userId:this.user.userId}
            foodReduceOneNum(data).then(res=>{
                let result=res.data.data
                this.shopcartList=result.foodVoList
                this.cartObj=result.cart
            })
        },
        clearCartButton(){
            let data={cartId:this.user.cartId}
            clearCart(data).then(res=>{
                  this.$router.push({name:'blank'}) 
                   setTimeout(() => {
                        this.$router.push({name:'clientFoodList',query:{canteenId:this.canteenId}})
                   }, 1); 
            })
        },
        buyButton(){
            let addressId=this.selectAddressId
            let orderTime=''
            if(!this.chooseTime){
                orderTime=this.nowDay+':'+this.nowHour+':'+this.nowMinute
            }
            else{
              orderTime=this.nowDay+':'+this.chooseTime
            }           
            let orderRemark=this.orderRemark
            if(!addressId){
                 this.$Message.error('请先选择地址')
                return
            }
            let data={addressId:addressId,orderTime:orderTime,orderRemark:orderRemark,userId:this.user.userId}     
            addOneOrder(data).then(res=>{
                this.$Message.success('下单成功')
                this.$router.push({name:'canteenList'})
            })                   
        },
        handleChange(time){
            this.chooseTime=time
        },
        //点击确定按钮后触发的事件
        ok(){
                        let addressId=this.selectAddressId
            let orderTime=''
            if(!this.chooseTime){
                orderTime=this.nowDay+':'+this.nowHour+':'+this.nowMinute
            }
            else{
              orderTime=this.nowDay+':'+this.chooseTime
            }           
            let orderRemark=this.orderRemark
            if(!addressId){
                 this.$Message.error('请先选择地址')
                return
            }
            let data={addressId:addressId,orderTime:orderTime,orderRemark:orderRemark,userId:this.user.userId}     
            addOneOrder(data).then(res=>{
                this.$Message.success('下单成功')
                this.$router.push({name:'canteenList'})
            })  
        },
        addButton(){
            this.$router.push({name:'addresspage'})
        },
        //点击取消后触发的事件
        cancel(){
            debugger
            this.$Message.error('付款失败')
        }
    },

}
</script>
<style lang="less">
.orderpage{ 
       min-height:800px;
    .pp{
        font-size: 20px;
    }
    .left{
        display:inline-block;
        width:350px;
        margin:0px 20px 0px 10px;

    }
    .right{
        display:inline-block;
        width:500px;
        .card{
        display:inline-block;
        width:700px;
        margin:10px 0px 20px 0px;
    }
    }
}
</style>

